.encabezado h2 {
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: center;
}

.menu-horizontal {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  background-color: rgba(0, 0, 0, 0.5);
  align-items: center;
}
.menu-horizontal a {
  font-size: 2rem;
}

.menu-horizontal > li > a {
  display: flex;
  padding: 15px 20px;
  color: white;
  text-decoration: none;
}

.menu-horizontal > li:hover {
  background-color: red;
}

.menu-vertical {
  position: absolute;
  display: none;
  list-style-type: none;
  padding: 0;
  width: 200px;
  background-color: rgba(0, 0, 0, 0.5);
}

.menu-horizontal li:hover .menu-vertical {
  display: block;
}

.menu-vertical li:hover {
  background-color: black;
}

.menu-vertical li a {
  display: block;
  color: white;
  text-decoration: none;
  padding: 15px 15px 15px 20px;
}